define('client/helpers/format-currency', ['exports'], function (exports) {

    'use strict';

    exports['default'] = Ember.Helper.helper(function (params) {
        var value = params[0],
            dollars = Math.floor(value / 100),
            cents = value % 100,
            sign = '$';

        if (cents.toString().length === 1) {
            cents = '0' + cents;
        }

        if (parseInt(value) == 0) {
            return "Free";
        } else {
            return '' + sign + dollars + '.' + cents;
        }
    });

});